import React from 'react';
import glamorous from 'glamorous';

import Container from '../components/Container';
import DisplayText from '../components/DisplayText';
import PresentingText from '../components/PresentingText';
import Project from '../components/Project';

import AppStoreIcon from '../images/app-store-icon.png';
import OCDExhibitImage from '../images/ocd-exhibit.jpg';
import PublicPressWebsite from '../images/public-press-website.png';
import PublicPressApollo from '../images/public-press-apollo.jpg';
import PublicPressGeneral from '../images/public-press-general.jpg';
import ShopifyDashboardImage from '../images/shopify-merchant-overview-dashboard.png';

export default function App() {
  return (
    <Container>
      <glamorous.Header
        color="tomato"
        display="flex"
        flexDirection="column"
        height="90vh"
        justifyContent="center"
      >
        <DisplayText>
          <PresentingText startAnimation>
            hi 
          </PresentingText>
        </DisplayText>
        <DisplayText>
          <PresentingText startAnimation delay={300}>
            my name is andy.
          </PresentingText>
        </DisplayText>
        <DisplayText>
          <PresentingText startAnimation delay={900}>
            i'm a software developer 
          </PresentingText>
        </DisplayText>
        <DisplayText>
          <PresentingText startAnimation delay={1100}>@</PresentingText>
          <PresentingText startAnimation delay={1200}>
            <a href="https://shopify.com" target="_blank">
              shopify
            </a>
          </PresentingText>
        </DisplayText>
        <DisplayText>
          <PresentingText startAnimation delay={1300}>
            living in chicago 
          </PresentingText>
        </DisplayText>
      </glamorous.Header>

      <glamorous.Section>
        <DisplayText>
          <PresentingText startAnimation delay={2400}>
            stuff i've done &darr;
          </PresentingText>
        </DisplayText>

        <Project
          title="merchant analytics @ shopify"
        >
          <p>
            I joined the merchant analytics team at Shopify as an intern in May 2016. After a few months as a
            part-time employee while I finished my degree at Brigham Young University, I joined the team full-time
            in January, 2017.
          </p>
          <img src={ShopifyDashboardImage} />
          <p>
            Shortly after I joined, we began rewriting our product with React, culminating in a string of releases in
            Summer 2017. One project I had the pleasure of working on was
            the <a href="https://www.shopify.com/blog/merchant-overview-dashboard" target="_blank">Merchant Overview Dashboard</a> which
            gave powerful analytical tools to all merchants who use Shopify including information about sales
            and traffic with the ability to compare across time periods to monitor a shop's performance.
          </p>
        </Project>

        <Project
          title="OCD Understood"
          githubUrl="https://github.com/asmockler/OCD"
          projectUrl="https://brookealius.myportfolio.com/ocd-understood-bfa-capstone"
        >
          <p>
            While studying at Brigham Young University in early 2016, I was approached by Brooke Alius, a senior
            in the Graphic Design program. For her final project, she wanted to build an interactive exhibit that
            would help visitors understand Obsessive Compulsive Disorder.
          </p>
          <p>
            With her designs, and help from my good friend <a href="https://github.com/danmo91" target="_blank">Dan Morain</a>, I built an iPad
            game that served both to catch the interest of those visiting the exhibit and to educate about what is is like to
            suffer from OCD and how its symptoms can be managed.
          </p>
          <img src={OCDExhibitImage} />
          <p>
            Following the success of the exhibit while on display at BYU, Brooke was asked to present at the IOCDF's 24th annual OCD Conference
            in July, 2017. I rebuilt the app to prepare for a general release and it is now available on all iOS
            devices via the Apple App Store.
          </p>
          <p>
            <a href="https://itunes.apple.com/us/app/ocd-understood/id1252841881" target="blank">
              <img src={AppStoreIcon} />
            </a>
          </p>
        </Project>

        <Project
          title="Public Press"
          projectUrl="https://publicpress.me"
        >
          <p>
            In November 2015 I started Public Press along with my friend Hannah and my wife Haylee. Inspired by the idea of
            displaying our favorite books more prominently in our homes, we began printing posters that display the full
            text of classic books, all on one page.
          </p>
          <glamorous.Div display="flex">
            <glamorous.Div flex="0 0 50%"><glamorous.Img src={PublicPressGeneral} paddingRight="30px" /></glamorous.Div>
            <glamorous.Div flex="0 0 50%"><glamorous.Img src={PublicPressApollo} paddingLeft="30px" /></glamorous.Div>
          </glamorous.Div>
          <p>
            As demand for our product grew, we purchased a printer capable of printing very fine text and opened an online
            store through which we could accept orders. We continue to use Shopify to sell our prints. Our products are also
            carried at two locations in Utah and are available through various other online outlets.
          </p>
          <p>
            If you are interested in keeping up with what Public Press is doing, you
            can <a href="https://www.instagram.com/public_press/" target="_blank">follow us on Instagram</a>.
          </p>
        </Project>

        <Project title="everything else">
          <p>
            I have lots of other projects that don't deserve their own section but are still fun to talk about,
            from <a href="https://github.com/asmockler/telescope" target="_blank">Telescope </a> (a Snapchat Spectacles notifier)
            to <a href="https://github.com/asmockler/KatrinaRicksBFA" target="_blank">an interactive fine arts project</a> built with Katrina Ricks,
            I am always looking for fun new things to do. You
            can <a href="https://github.com/asmockler" target="_blank">follow me on GitHub</a> to see what I've been up to.
          </p>
          <p>
            In my spare time, I dream about starting my own clothing line, running a presidential campaign for pizza,
            or opening mochi ice cream vending machines. I am
            a <a href="https://www.youtube.com/watch?v=AemQSowyh8A" target="_blank">very amateur drone videographer</a> and post
            videos from time to time on my youtube channel.
          </p>
          <p>
            Most of all, I like eating toomuchfood and watching Gilmore Girls with my wife.
          </p>
        </Project>
      </glamorous.Section>

      <glamorous.Footer
        display="flex"
        padding="100px 0 200px"
      >
        <glamorous.Div
          flex="0 0 auto"
          paddingRight="50px"
        >
          <DisplayText>get</DisplayText>
          <DisplayText>in</DisplayText>
          <DisplayText>touch</DisplayText>
          <DisplayText>&rarr;</DisplayText>
        </glamorous.Div>
        <glamorous.Div
          flex="1 0 auto"
        >
          <p>
            <a href="https://github.com/asmockler" target="_blank">on github</a>
          </p>
          <p>
            <a href="https://twitter.com/andymockler" target="_blank">on twitter</a>
          </p>
          <p>
            <a href="mailto:asmockler@gmail.com" target="_blank">by email</a>
          </p>
        </glamorous.Div>
      </glamorous.Footer>
    </Container>
  );
}



